.Centerlog{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0px 7px 29px 0px rgba(100, 100, 111, 0.25);
  border-radius: 10px;
}
.auth-multi-layout {
    position: relative;
    width: 100%;
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--bg-auth);
  }
  .auth-box {
    width: 460px;
    background: #ffffff;
    border-radius: 10px;
    box-shadow: 0px 7px 29px 0px rgba(100, 100, 111, 0.25);
  }
  .auth-header {
    padding: 1.4rem 1.5rem 0.5rem 1.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
  .auth-header-logo-img {
    width: 100px;
  }
  .forgot-img .auth-header-logo-img {
    width: 200px;
  }
  .auth-header-title {
    font-size: 20px;
    font-weight: 500;
    margin-bottom: 0;
    margin-top: 10px;
    color: #212529;
  }
  .auth-header-subtitle {
    font-size: small;
    margin-bottom: 0;
    color: #6c757d;
  }
  .forgot-subtitle {
    margin-bottom: 10px;
  }
  .auth-body {
    padding: 0.5rem 1.5rem 1.5rem 1.5rem;
  }
  .input-field {
    position: relative;
    width: 100%;
    margin-bottom: 15px;
  }
  .input-label {
    display: flex;
    color: #000000;
  }
  .input-control {
    display: block;
    width: 100%;
    height: 35px;
    border-radius: 5px;
    border: 1px solid #8e8e97;
    padding: 0.25rem 0.67rem;
    outline: none;
    transition: border 0.1s ease-in, box-shadow 0.15s ease-in;
  }
  .input-control::placeholder {
    transition: transform 0.1s ease-in;
  }
  .input-control:focus::placeholder {
    transform: translateX(5px);
  }
  .input-control:focus,
  .input-control:valid {
    border: 2px solid #31b675;
  }
  .link-end {
    margin: 5px 0;
    text-decoration: none;
    color: #31b675;
  }
  .link-end:hover {
    color: #369e6c;
  }
  .btn-submit {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    text-decoration: none;
    cursor: pointer;
    padding: 0.35rem 1.24rem;
    border-radius: 5px;
    border: 1px solid #31b675;
    background: #31b675;
    color: #ffffff;
    width: 100%;
  }
  .btn-submit:hover {
    background: #31b675;
  }
  .flex-end {
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  .text-center {
    text-align: center;
    font-size: 15px;
    color: #75757c;
    margin: 15px 0;
  }
  .link-text-center {
    text-decoration: none;
    color: #31b675;
  }
  .link-text-center:hover {
    color: #31b675;
  }
  .btn-back-to-login {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    text-decoration: none;
    cursor: pointer;
    padding: 0.35rem 1.24rem;
    border-radius: 5px;
    border: 1px solid transparent;
    background: transparent;
    color: #31b675;
    margin-top: 5px;
    width: 100%;
  }
  .btn-back-to-login:hover {
    color: #31b675;
  }